body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

* {
  box-sizing: border-box;
}

.watch {
  border: 5px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch__center {
  z-index: 1;
  background-color: #fd0;
  border-radius: 50%;
  width: 35px;
  height: 35px;
}

.watch__minutes {
  background-color: green;
  border-radius: 10px;
  width: 15px;
  height: 30%;
  animation: 60s linear infinite rotate;
  position: absolute;
  bottom: 50%;
}

.watch__seconds {
  background-color: #0057b7;
  border-radius: 10px;
  width: 10px;
  height: 50%;
  animation: 5s linear infinite rotate;
  position: absolute;
  bottom: 50%;
}

@keyframes rotate {
  from {
    transform-origin: bottom;
    transform: rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}

